import React, { Component } from 'react';
import Header from './Header';
import BgBubbles from './BgBubbles';
import $ from 'jquery';
import './css/LoginForms.css';
import './css/Restore.css';
import ModalWindow from './Modal';

class Restore extends Component {
  constructor(props){
    super(props);
    this.state = {
      pw: '',
      confirmed: '',
      modalText: '',
      modal: false,
      isPassHidden: true,
    };
    this.openModal         =   this.openModal.bind(this);
    this.handleSubmit      =   this.handleSubmit.bind(this);
    this.showPassword      =   this.showPassword.bind(this);
  }

  openModal(text) {
    this.setState({ modal: true, modalText: text});
  }

  handleChange = (event) => {
    //         
    this.setState({
      [event.target.name]: event.target.value
    })
  }

  handleSubmit(e) {
    var self = this;
    if (self.state.pw === self.state.confirmed && self.state.pw !== '' && self.state.confirmed !== '') {
      self.setState({ modal: false });
      e.preventDefault(); 
      const formData = new FormData();  
      
      function getUrlVars() {
        let vars = {};
        let parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
            vars[key] = value;
        });
        return vars;
      }

      formData.append('password',    $('input[name="pw"]').val());
      formData.append('auth', getUrlVars()['auth']) 

      $.ajax({
        url         : window.location.origin + '/api/user/restore.php',
        data        : formData,
        processData : false,
        contentType : false,
        type: 'POST',
        success: function(res) {
          console.log(res);
          let response = res.result;

          switch(response) {
            case   0:
              self.openModal("   .");
              break;
            case   1:
              self.openModal(",     -      .");
              window.location.href = 'login';
              break;
            default: return null;
            }
          },
        error: function(err) {
          self.openModal(" .");
        }
      });    
    } 
  }

  showPassword() {
    this.setState({ isPassHidden: !this.state.isPassHidden});
  }

  render() {
    return (
      <div className="login-screen">
        <Header />
        <div className="form-container restore-container">
            <p className="restore-container-head"><b>  .</b></p><p className="restore-container-note">          .</p>
              <div className="form-group">
                <label htmlFor="pw"> </label><br />
                <input
                  className="form-control"
                  id="pw"
                  name="pw"
                  type="password"
                  value={this.state.pw}
                  onChange={this.handleChange}
                  required
                />
                {this.state.pw.length < 8 && this.state.pw !== '' ? <p className="invalid-form-result">     8 </p> : null}    
              </div>

              <div className="form-group">
                <label htmlFor="confirmed"> </label><br />
                <input
                  className="form-control"
                  id="confirmed"
                  name="confirmed"
                  type={this.state.isPassHidden ? 'password' : 'text'}
                  value={this.state.confirmed}
                  onChange={this.handleChange}
                  required
                /><span className="password-eye" onClick={this.showPassword}></span>
                {this.state.pw !== this.state.confirmed && this.state.confirmed !== '' ? <p className="invalid-form-result">  </p> : null}
              </div> 
            <button className="btn" onClick={this.handleSubmit}> </button>             
        </div>
        <BgBubbles /> 
        {this.state.modal ? <ModalWindow text={this.state.modalText} /> : null}
      </div>
    );
  }
}

export default Restore;