import React, { Component } from 'react';
import $ from 'jquery';
import Cookie from './functions/Cookie';
import ForgetPassword from './ForgetPassword';
import ModalWindow from './Modal';

class SignupForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      login: '',
      password: '',
      isForget: '',
      modalText: '',
      modal: false,
      isPassHidden: true,
    }
    this.handleChange      =   this.handleChange.bind(this);
    this.handleSubmit      =   this.handleSubmit.bind(this);
    this.openModal         =   this.openModal.bind(this);
    this.showPassword      =   this.showPassword.bind(this);
  }

  //    
  handleChange(event) {
    //         
    this.setState({
      [event.target.name]: event.target.value
    })
  }

  openModal(text) {
    this.setState({ modal: true, modalText: text});
  }

  forgetPassword = () => this.setState({ isForget: !this.state.isForget });

  handleSubmit(e) {
    var self = this;
    self.setState({ modal: false });
    e.preventDefault(); 

    if (this.state.login !== '' && this.state.password !== '') {
      const formData = new FormData();
        
      formData.append('login',    $('input[name="login"]').val());
      formData.append('password', $('input[name="password"]').val());

      $.ajax({
        
        url         : 'http://akvatory.local/api/user/signup.php',
        /* url         : window.location.origin + '/api/user/signup.php', */
        data        : formData,
        processData : false,
        contentType : false,
        type: 'POST',
        success: function(res) {
          console.log(res);
          let response = res.result;
          let user_id = res.user_id;
          let cookie = new Cookie();
          
          switch(response) {
            case   0:
              self.openModal('  .    ');
              break;
            case 0.5:
              self.openModal(' ,      ,   .');
              break;
            case   1:
              cookie.setCookie('user_id', user_id, 30);
              window.location.href = `/`;
              break;
          }
        },
        error: function(err) {
          self.openModal(' .');
        }
      });

    } else {
      self.openModal(',   .');
    }
  }

  showPassword() {
    this.setState({ isPassHidden: !this.state.isPassHidden});
  }

  render() {
    return (
      <>
      {this.state.modal ? <ModalWindow text={this.state.modalText} /> : null}
      <div className="form-container" style={{'paddingBottom':'45px'}}>   
        <form className="registerForm" id="register" action="" method="POST" onSubmit={this.handleSubmit}>
          <div className="form-group">
            <label htmlFor="login">  email</label><br />
            <input
              className="form-control"
              id="login"
              name="login"
              type="text"
              value={this.state.username}
              onChange={this.handleChange}
              required
            />    
          </div>
          <div className="form-group">
            <label htmlFor="password"></label><br />
            <input
              className="form-control"
              id="password"
              name="password"
              type={this.state.isPassHidden ? 'password' : 'text'}
              value={this.state.password}
              onChange={this.handleChange}
              required
            /><span className="password-eye" onClick={this.showPassword}></span>
             {this.state.password.length < 8 && this.state.password !== '' ? <p className="invalid-form-result">     8 </p> : ''}
          </div>
          <div className="register-buttons">
            <a onClick={this.forgetPassword} className="forgive-password"> ?</a>
            <button className="btn"></button>
          </div>
          {this.state.isForget ? <ForgetPassword forgetPassword={this.forgetPassword} /> : null}
        </form>  
      </div> 
      </>
    )
  }
}

export default SignupForm;